.return {
    overflow-y: auto;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    &-item {
        display: flex;
        height: 60px;
        line-height: 60px;
        width: 100%;
        box-sizing: border-box;
        border-bottom: 1px solid #000;
        &:last-child {
            border: none;
        }
        .black {
            background-color: #000;
            color: #fff;
        }
        .white-line {
            border-right: 1px solid #fff;
        }
        &-col-1 {
            padding: 0 20px;
            box-sizing: border-box;
            border-right: 1px solid #000;
            width: 500px;
            p {
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
        &-col-2 {
            padding: 0 20px;
            box-sizing: border-box;
            border-right: 1px solid #000;
            flex: 1 0 0;
        }
        &-col-3 {
            padding: 0 20px;
            box-sizing: border-box;
            border-right: 1px solid #000;
            flex: 1 0 0;
        }
        &-col-4 {
            padding: 0 20px;
            box-sizing: border-box;
            flex: 1 0 0;
        }
    }
}